import {
    Component,
    Host,
    h,
    Prop,
    Event,
    EventEmitter,
    Fragment,
    State,
    Element,
} from '@stencil/core';
import { classlist } from '../../../utils/classlist';
import type { MessageRole } from '../../../types';
import { fetchTranslations } from '../../../utils/translation';

// Add vote status enum to match Angular implementation
export enum VoteStatus {
    None = 0,
    Up = 1,
    Down = -1,
}

const messageVariantsConfig = {
    variants: {
        role: {
            user: 'bg-surface-2 text-dark ml-auto inline-block p-8',
            assistant: 'text-dark mr-auto',
        },
        loading: {
            true: 'animate-pulse',
            false: '',
        },
    },
    defaultVariants: {
        role: 'user',
        loading: false,
    },
};

const getMessageClasses = classlist(
    'rounded-lg py-4 text-base',
    messageVariantsConfig,
);

/**
 * @category Prompt
 * @description A message component for displaying individual chat messages with different roles (user, assistant). Supports optional avatars, loading states, error states, and interactive actions like copy, edit, and retry.
 * @slot - Custom message content (alternative to using the content prop)
 */
@Component({
    tag: 'atui-prompt-message',
    shadow: false,
})
export class AtuiPromptMessage {
    /**
     * The role/type of the message sender (only 'user' and 'assistant' are supported)
     */
    @Prop() role: Exclude<MessageRole, 'system'> = 'user';

    /**
     * The message content text
     */
    @Prop() content: string = '';

    /**
     * Display name for the message sender
     */
    @Prop() name: string;

    /**
     * Shows loading state with animated placeholder content
     */
    @Prop() loading: boolean = false;

    /**
     * Shows error state styling and enables retry action
     */
    @Prop() error: boolean = false;

    /**
     * Custom error message text (defaults to generic error message)
     */
    @Prop() error_message: string;

    /**
     * The current vote status of the message
     */
    @Prop() vote_status: VoteStatus = VoteStatus.None;

    /**
     * Display voting actions for assistant messages
     */
    @Prop() enable_vote: boolean = false;

    /**
     * Display copy action for assistant messages - copies message content to clipboard
     */
    @Prop() enable_copy: boolean = false;

    /**
     * Display edit action for user messages
     */
    @Prop() enable_edit: boolean = false;

    /**
     * Unique identifier for the message
     */
    @Prop() message_id: string;

    /**
     * Emitted when the copy action is triggered
     */
    @Event() atuiCopy: EventEmitter<string>;

    /**
     * Emitted when a vote action is triggered
     */
    @Event() atuiVote: EventEmitter<{ messageId: string; score: number }>;

    /**
     * Emitted when the retry action is triggered (for assistant messages with errors)
     */
    @Event() atuiRetry: EventEmitter<void>;

    /**
     * Emitted when the edit action is triggered (for user messages)
     */
    @Event() atuiEdit: EventEmitter<string>;

    @State() translations: any;
    @State() copyFeedbackVisible: boolean = false;
    errorEl: HTMLDivElement;
    @Element() el: HTMLAtuiInputElement;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    private handleCopy = async () => {
        try {
            await navigator.clipboard.writeText(this.content);
            this.copyFeedbackVisible = true;
            this.atuiCopy.emit(this.content);

            setTimeout(() => {
                this.copyFeedbackVisible = false;
            }, 2000);
        } catch (err) {
            console.error('Failed to copy text:', err);
        }
    };

    private handleRetry = () => {
        this.atuiRetry.emit();
    };

    private handleEdit = () => {
        this.atuiEdit.emit(this.content);
    };

    private handleVote = (score: number) => {
        const newScore = this.vote_status === score ? VoteStatus.None : score;
        this.atuiVote.emit({ messageId: this.message_id, score: newScore });
    };

    private renderActions() {
        if (this.loading) return null;

        return (
            <div
                class="mt-2 flex items-center justify-end gap-2"
                data-name="message-actions"
            >
                {this.role === 'user' && this.enable_edit && (
                    <atui-tooltip position="top">
                        <atui-button
                            slot="tooltip-trigger"
                            size="sm"
                            type="secondaryText"
                            icon="edit"
                            class="text-xs"
                            onClick={this.handleEdit}
                            data-name="edit-button"
                        />
                        <span>
                            {this.translations?.ATUI?.PROMPT?.EDIT || 'Edit'}
                        </span>
                    </atui-tooltip>
                )}

                {this.role === 'assistant' && this.enable_vote && (
                    <Fragment>
                        <atui-tooltip position="top">
                            <atui-button
                                slot="tooltip-trigger"
                                size="sm"
                                type="secondaryText"
                                onClick={() => this.handleVote(VoteStatus.Up)}
                                data-name="vote-up-button"
                            >
                                {this.vote_status === VoteStatus.Up ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        height="15px"
                                        viewBox="0 -960 960 960"
                                        width="15px"
                                        fill="#000000"
                                    >
                                        <path d="M720-144H264v-480l288-288 32 22q18 13 26.5 33t3.5 41l-38 192h264q30 0 51 21t21 51v57q0 8-1.5 14.5T906-467L787-188q-9 20-27 32t-40 12Z" />
                                    </svg>
                                ) : (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        height="15px"
                                        viewBox="0 -960 960 960"
                                        width="15px"
                                        fill="#adb5bd" // token-border-dark
                                    >
                                        <path d="M720-144H264v-480l288-288 32 22q17 12 26 30.5t5 38.5l-1 5-38 192h264q30 0 51 21t21 51v57q0 8-1.5 14.5T906-467L786.93-187.8Q778-168 760-156t-40 12Zm-384-72h384l120-279v-57H488l49-243-201 201v378Zm0-378v378-378Z" />
                                    </svg>
                                )}
                            </atui-button>
                            <span>
                                {this.translations?.ATUI?.PROMPT?.HELPFUL ||
                                    'Helpful'}
                            </span>
                        </atui-tooltip>

                        <atui-tooltip position="top">
                            <atui-button
                                slot="tooltip-trigger"
                                size="sm"
                                type="secondaryText"
                                onClick={() => this.handleVote(VoteStatus.Down)}
                                data-name="vote-down-button"
                            >
                                {this.vote_status === VoteStatus.Down ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        height="15px"
                                        viewBox="0 -960 960 960"
                                        width="15px"
                                        fill="#000000"
                                    >
                                        <path d="M240-816h456v480L408-48l-32-22q-18-13-26.5-33t-3.5-41l38-192H120q-30 0-51-21t-21-51v-57q0-8 1.5-14.5T54-493l119-279q8-20 26.5-32t40.5-12Z" />
                                    </svg>
                                ) : (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="15px"
                                        viewBox="0 -960 960 960"
                                        height="15px"
                                        fill="#adb5bd" // token-border-dark
                                    >
                                        <path d="M240-816h456v480L408-48l-32-22q-17-12-26-30.5t-5-38.5l1-5 38-192H120q-30 0-51-21t-21-51v-57q0-8 1.5-14.5T54-493l119-279q8-20 26.5-32t40.5-12Zm384 72H240L120-465v57h352l-49 243 201-201v-378Zm0 378v-378 378Z" />
                                    </svg>
                                )}
                            </atui-button>
                            <span>
                                {this.translations?.ATUI?.PROMPT?.NOT_HELPFUL ||
                                    'Not Helpful'}
                            </span>
                        </atui-tooltip>
                    </Fragment>
                )}

                {this.role === 'assistant' && this.enable_copy && (
                    <atui-tooltip position="top">
                        <atui-button
                            slot="tooltip-trigger"
                            size="sm"
                            icon={
                                this.copyFeedbackVisible
                                    ? 'check'
                                    : 'content_copy'
                            }
                            type="secondaryText"
                            onClick={this.handleCopy}
                            data-name="copy-button"
                        />
                        <span>
                            {this.copyFeedbackVisible
                                ? this.translations?.ATUI?.PROMPT?.COPIED ||
                                  'Copied'
                                : this.translations?.ATUI?.PROMPT?.COPY ||
                                  'Copy'}
                        </span>
                    </atui-tooltip>
                )}

                {this.role === 'assistant' && this.error && (
                    <atui-tooltip position="top">
                        <atui-button
                            slot="tooltip-trigger"
                            size="sm"
                            type="secondaryText"
                            icon="refresh"
                            onClick={this.handleRetry}
                            data-name="retry-button"
                        />
                        <span>
                            {this.translations?.ATUI?.PROMPT?.RETRY || 'Retry'}
                        </span>
                    </atui-tooltip>
                )}
            </div>
        );
    }

    private renderContent() {
        if (this.loading) {
            return (
                <div
                    class="py-8"
                    data-name="loading-wrapper"
                >
                    <atui-loading
                        class="flex items-center gap-4 py-8"
                        data-name="loading-content"
                        variant="typing"
                        size="sm"
                    />
                </div>
            );
        }

        if (this.error) {
            return (
                <div
                    class="space-y-2"
                    ref={(el) => (this.errorEl = el)}
                    data-name="error-content"
                >
                    <p class="text-destructive-foreground">
                        {this.error_message ||
                            this.translations?.ATUI?.PROMPT
                                ?.ERROR_GENERATING_RESPONSE ||
                            'Error generating response'}
                    </p>
                </div>
            );
        }

        return (
            <div
                class="prose prose-sm preserve-newlines max-w-none"
                data-name="message-content"
            >
                <slot></slot>
                {this.content}
            </div>
        );
    }

    render() {
        const messageClasses = getMessageClasses({
            role: this.role,
            loading: this.loading,
        });

        return (
            <Host
                class="flex w-full gap-8"
                data-name="message-container"
                data-role={this.role}
            >
                <div class="flex flex-1 flex-col">
                    {this.name && (
                        <span
                            class="text-light self-start text-sm"
                            data-name="message-name"
                        >
                            {this.name}
                        </span>
                    )}

                    <div class={messageClasses}>{this.renderContent()}</div>

                    {this.renderActions()}
                </div>
            </Host>
        );
    }
}
